<template>
	<div id="app"> 
		<div class="codeBox">
			<pre id="code">{{code}}</pre>
		</div>
		<div v-html="styleCode">
		</div>
		<div class="resume" id="resume">
			<h1>Hello World</h1>
			<dl>
				<dt>张祥</dt>
				<dd><strong>性别：</strong>男</dd>
				<dd><strong>学历：</strong>大专</dd>
				<dd><strong>邮箱：</strong>spirt108@outlook.com</dd>
			</dl>
			<p>应聘前端开发，本人熟练使用jQuery,vue及各种类库,会组件/插件开发，能脱离库书写JS代码编码,常思其健壮性，扩展性，维护性</p>
			<h2>技能/skill</h2>
			<ol>
				<li><i>html+css</i><div class="skilled"><span></span></div></li>
				<li><i>javaScript</i><div class="skilled"><span></span></div></li>
				<li><i>photoshop</i><div class="skilled"><span></span></div></li>
				<li><i>jQuery</i><div class="skilled"><span></span></div></li>
				<li><i>bootstrap</i><div class="skilled"><span></span></div></li>
				<li><i>vue</i><div class="general"><span></span></div></li>
			</ol>
			<h2>链接/link</h2>
			<ul>
				<li><a href="https://github.com/spiritzx">我的github</a></li>
				<li><a href="http://blog.csdn.net/qq_37522454">我的blog</a></li>
			</ul>
			<div>喜欢尝试，期待新鲜事物,前端是兴趣也是未来,带上我吧，一起看到更大的世界</div>
		</div>
	</div>
</template>

<script>
export default {
	name: 'app',
	data () {
		return {
			top: '',
			code: ``,
			endCode:
`/*resume 1.0*/
/*
＊你好，我是张祥，我最近在找前端工作
＊找个工作不容易所以得做个酷炫的的简历
＊这份简历是模仿了网上大神，并且简化了一点代码
＊运用了webpack＋vue2.0＋css3
*/
/*纯白色好像不好看，那给背景加个我喜欢的颜色吧*/
html {background: #0080ff; font-size: 62.5%;}
/*字体也可以改一改*/
body {font: 2.0rem/1.2em '宋体' }
/*感觉需要一个好看盒子把代码装起来*/
.codeBox {
	overflow: hidden;
	position: relative;
	width: 45vw;
	height: 90vh;
	padding: 10px;
	background: #039;
	color: #ccc;
	margin: 20px;
	border-radius: 10px;
	box-shadow: 5px 5px 10px #333;
}
.codeBox #code {
	position: absolute;
}
/*来点3D效果*/
#app {
	width: 100%;
	height: 100%;
	position: relative;
	transform-style: preserve-3d;
	perspective: 800px;
	-webkit-perspective: 800px;
	perspective-origin: -250px;
	-webkit-perspective-origin: -250px;
}
#app .codeBox {
	position: absolute;
	transform: rotateY(10deg) translateZ(-70px);
	-webkit-transform: rotateY(10deg) translateZ(-70px);
}
/*接下来我可以写简历了*/
.resume {
	padding: 10px;
	position: absolute;
	right: 20px;
	top: 20px;
	width: 45vw;
	background: #fff;
	height: 90vh;
}
/*修饰一下就ok了*/
.resume h1 {
	margin: 0px;
	padding: 0px;
	height: 40px;
	line-height: 40px;
	font-size: 36px;
	font-weight: bold;
	text-align: center;
}
.resume dt {
	font-size: 24px;
	font-weight: bold;
	height：30px;
	line-height: 30px;
}
.resume ol li {
	height: 30px;
	margin-bottom: 5px;
	line-height: 30px;
}
.resume i {
	display: inline-block;
	vertical-align:top;
	width: 20%;
	text-align: left;
}
.resume ol li div {
	display: inline-block;
	vertical-align:top;
	width: 60%;
	height: 28px;
	border: 1px solid #ccc;
	border-radius: 14px;
	overflow: hidden;
}
.resume .skilled span {
	display: block;
	background: #080;
	width: 70%;
	height: 28px;	
}
.resume .general span {
	display: block;
	background: #880;
	width: 50%;
	height: 28px;
}
.resume>div {
	background: #ccc;
	height: 40px;
	line-height: 40px;
	text-align: center;
}
`,
			styleCode: '',
		}
	},
	methods: {
		move: function () {
			var oCode = document.getElementById('code');
			var oCodeBox = document.getElementsByClassName('codeBox')[0];
			var oCodeH = oCode.offsetHeight;
			var oCodeBoxH = oCodeBox.offsetHeight;
			if (oCodeBoxH < oCodeH) {
				oCode.style.top = -parseInt((oCodeH - (oCodeBoxH - 20))) + 'px';
			}
		}
	},
	created() {
		var n = 0;
		var top = document.getElementById('code');
		var timer = setInterval(() => {
			this.code = this.endCode.substring(0, n);
			this.styleCode = '<style>' + this.code + '</style>';
			n++;
			if (n >= this.endCode.length) {
				clearInterval(timer);
				timer = null;
			};
			this.move();
		}, 30);
	},
}
</script>

<style>
	.resume {
		height: 0px;
		transition: height 2s;
		overflow: hidden;
	}
	.resume ol li  span {
		width: 0px;
		transition: width 1s;
	}
</style>


